import PlayOut from "../pages/Home/PlayOut";

const routes = [
    {
        path: '/',
        redirect: '/login'
    },
    {
        path: '/home',
        element: <PlayOut/>,
        //  element 
        children: [
            {
                path: 'teacher',
                component: () => import('../pages/Index/Teacher'),
                // component 
                meta: {
                    title: '',
                    Auth: 'teacher'
                    //  App.js 
                }
            },
        ]
    },
    {
        path: '/login',
        component: () => import('../pages/User/Login'),
        meta: {
            title: '',
            Auth: 'public'
        }
    },
    {
        path: '/register',
        component: () => import('../pages/User/Register'),
        meta: {
            title: '',
            Auth: 'public'
        }
    },
    {
        path: '/Spin',
        component: () => import('../component/loading/Spin'),
        meta: {
            Auth: 'public'
        }
    },
    {
        path: '/403',
        component: () => import('../pages/Error/403'),
        meta: {
            title: '403',
            Auth: 'public'
        }
    }, {
        path: '/404',
        component: () => import('../pages/Error/404'),
        meta: {
            title: '404',
            Auth: 'public'
        }
    },
    {
        path: '/500',
        component: () => import('../pages/Error/500'),
        meta: {
            title: '500',
            Auth: 'public'
        }
    }, {
        path: '*',
        redirect: '/404',
    }
]

export default routes