import {Menu} from 'antd';
import {EditOutlined, FormOutlined, BarChartOutlined, HomeOutlined, CommentOutlined} from '@ant-design/icons';
import React from 'react';
import './index.scss'
import {Link} from 'react-router-dom';
import {useSelector} from "react-redux";

function getItem(label, key, icon, children, type) {
    return {
        key,
        icon,
        children,
        label,
        type,
    };
}

const teacher = [
    getItem((<Link to={'/home/teacher'}></Link>), 'home', <HomeOutlined/>),
    getItem('', 'apply', <EditOutlined/>, [
        getItem((<Link to={'/home/teacher/apply/departmentChange'}></Link>), 'departmentChange'),
        getItem((<Link to={'/home/teacher/apply/travelReimbursement'}></Link>), 'travelReimbursement'),
        getItem((<Link to={'/home/teacher/apply/leave'}></Link>), 'travelReimbursement'),
        getItem((<Link to={'/home/teacher/apply/procurement'}></Link>), 'travelReimbursement'),
        getItem((<Link to={'/home/teacher/apply/report'}></Link>), 'travelReimbursement'),
    ]),
    getItem('', 'record', <BarChartOutlined/>, [
        getItem((<Link to={'/home/teacher/record/departmentChange'}></Link>), 'leaveList'),
        getItem((<Link to={'/home/teacher/record/travelReimbursement'}></Link>), 'travelReimbursement'),
        getItem((<Link to={'/home/teacher/record/leave'}></Link>), 'travelReimbursement'),
        getItem((<Link to={'/home/teacher/record/procurement'}></Link>), 'travelReimbursement'),
        getItem((<Link to={'/home/teacher/record/report'}></Link>), 'travelReimbursement'),
    ]),
];
const department = [
    getItem((<Link to={'/home/department'}></Link>), 'department', <HomeOutlined/>),
    getItem('', 'apply', <FormOutlined/>, [
        getItem((<Link to={'/home/department/apply/notice'}></Link>), 'notice'),
        getItem((<Link to={'/home/department/apply/departmentChange'}></Link>), 'departmentChange'),
    ]),
    getItem('', 'sub4', <BarChartOutlined/>, [
        getItem('', '9'),
        getItem('', '10')
    ]),
];
const leader = [
    getItem((<Link to={'/home/department'}></Link>), 'home', <HomeOutlined/>),
    getItem('', 'apply', <FormOutlined/>, [
        getItem((<Link to={'/home/department/apply/notice'}></Link>), 'notice'),
        getItem((<Link to={'/home/department/apply/departmentChange'}></Link>), 'departmentChange'),
    ]),
    getItem('', 'notion', <CommentOutlined/>, [
        getItem((<Link to={'/notion/add'}></Link>), 'add'),
        getItem((<Link to={'/notion/show'}></Link>), 'show'),
    ]),
    getItem('', 'sub4', <BarChartOutlined/>, [
        getItem('', '9'),
        getItem('', '10')
    ]),
];

const RenderMenu = () => {
    const userType = useSelector(state => state.userType.value)
    // const [theme, setTheme] = useState('light');
    const RenderMenu = () => {
        switch (userType) {
            case 'teacher':
                return teacher
            case 'department':
                return department
            case 'leader':
                return leader
            default:
                return null
        }
    }
    const menu = RenderMenu()
    return (
        <Menu
            theme={"light"}
            mode="inline"
            items={menu}
            style={{minWidth: 0, flex: 'auto'}}
        />
    )
}

export default RenderMenu;